.step {
  position: relative;
  margin: 180px 0;
  font-size: 16px;
  font-size: 1.6rem;
  line-height: 1.5;
}

.step:last-child {
  margin-bottom: 0;
}

.step p {
  margin: 1em 0;
}

.step_simple {
  padding-top: 40px;
  margin: 0;
}

.step_simple .step__number {
  top: 0;
}

.step.is-active .step__number {
  color: red;
  text-shadow: 0 10px 30px rgba(255, 0, 0, 0.5);
}

.step__text {
  position: relative;
  width: 45.51282%;
  box-sizing: border-box;
}

.step__img {
  position: absolute;
  top: 50%;
  right: 30px;
  transform: translateY(-50%);
  width: 53.20513%;
  box-sizing: border-box;
}

.step__img img {
  display: block;
  max-width: 100%;
}

.step__img img[src*="step-1"] {
  max-width: 115%;
}

.step__number {
  position: absolute;
  z-index: -1;
  top: -67px;
  left: -48px;
  min-width: 80px;
  text-align: center;
  font-size: 140px;
  font-size: 14rem;
  line-height: 1;
  font-weight: 700;
  color: #EFF3F7;
  text-shadow: 1px 1px 0 red, -1px -1px 0 red, -1px 1px 0 red, 1px -1px 0 red;
}

.figure-small {
  width: 400px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.2);
}

.figure-small img {
  display: block;
  max-width: 100%;
}

.contacts {
  position: relative;
  margin-top: 87px;
}

.contacts .h1 {
  margin-bottom: 35px;
}

.contacts__left {
  width: 50%;
  padding-bottom: 80px;
}

.contacts__right {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 50%;
}

.contacts__right.bg {
  position: fixed;
  background: url(../img/contacts.jpg) no-repeat 50% 0;
  background-size: cover;
  top: 58px;
}

.contacts__right.bg * {
  display: none;
}

.contacts__head {
  padding-bottom: 25px;
  border-bottom: 1px solid #E0E4E9;
  font-size: 14px;
  font-size: 1.4rem;
  line-height: 1.71429;
}

.contacts__head .h1 {
  margin-bottom: 14px;
}

.contacts__head p {
  margin: 1em 0;
}

.contacts__head .select {
  margin: 10px 18px 0 0;
}

.contacts__section:not(:first-child) {
  margin-top: 60px;
}

.contatcs__section-head {
  margin-bottom: 30px;
}

.contatcs__section-head .select {
  margin-top: 10px;
}

.contacts-list {
  height: calc(100vh - 275px);
  overflow: hidden;
  margin-bottom: -80px;
  position: relative;
  -webkit-overflow-scrolling: touch;
}

.contacts-list::-webkit-scrollbar {
  width: 2px;
  background: white;
}

.contacts-list::-webkit-scrollbar-thumb {
  width: 2px;
  background: #EB2024;
}

.contacts-item {
  *zoom: 1;
}

.contacts-item:after {
  content: " ";
  display: table;
  clear: both;
}

.contacts-item_hover {
  cursor: pointer;
}

.contacts-item_hover:hover {
  background: #F7F9FB;
}

.contacts-item__inner {
  position: relative;
  float: right;
  width: 100%;
  max-width: 495px;
  box-sizing: border-box;
  padding-right: 40px;
}

.contacts-item__inner .h3 {
  padding: 10px 0;
}

.contacts-item__inner h4 {
  padding: 30px 0 0 0;
  margin: 0;
}

.contact {
  position: relative;
  padding-top: 15px;
  padding-bottom: 15px;
  font-size: 14px;
  font-size: 1.4rem;
  line-height: 1.71429;
}

.contact p {
  margin: 0.8em 0;
}

.contact p:first-child {
  margin-top: 0;
}

.contact p:last-child {
  margin-bottom: 0;
}

.contact dt {
  display: inline;
  color: #9FA5AC;
}

.contact dd {
  display: inline;
}

.contact_action {
  cursor: pointer;
}

.contact:hover {
  background: #F7F9FB;
}

.contact:hover .contact__actions {
  opacity: 1;
  visibility: visible;
}

.contact__title {
  display: block;
  font-size: 16px;
  font-size: 1.6rem;
  line-height: 1.5;
}

.contact__title + p {
  margin-top: 0;
}

.contact__pic {
  position: absolute;
  top: 0;
  right: 100%;
  margin-right: 20px;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  border: 1px solid black;
  box-sizing: border-box;
  overflow: hidden;
}

.contact__pic img {
  display: block;
  max-width: 100%;
  max-height: 100%;
}

.contact__actions {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  top: 50%;
  right: 40px;
  margin-top: -10px;
  user-select: none;
}

.contact__action-btn {
  display: inline-block;
  vertical-align: middle;
  color: #DBE1E7;
  font-size: 20px;
  font-size: 2rem;
  line-height: 1;
  transition: color 0.3s ease;
  margin-left: 22px;
}

.contact__action-btn .icon {
  fill: currentColor;
}

.contact__action-btn:hover {
  color: red;
}

#contacts-map {
  width: 100%;
  height: 100%;
}

.hero-picture {
  position: relative;
  background-size: cover;
  background-position: center;
}

.hero-picture:before {
  content: "";
  display: block;
  padding-bottom: 24.19355%;
}

.preview {
  display: block;
  position: relative;
  cursor: pointer;
  margin: 40px 0;
}

.preview img {
  display: block;
  max-width: 100%;
}

.preview__icon {
  position: absolute;
  top: 10px;
  right: 10px;
}

.preview__icon .icon {
  fill: white;
  font-size: 38px;
}

.features-block {
  margin: 60px 0;
}

.features-block:first-child {
  margin-top: 0;
}

.features-block:last-child {
  margin-bottom: 0;
}

.features-group {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.features-group > * {
  display: inline-block;
  vertical-align: middle;
}

.features-group .icon {
  font-size: 36px;
  vertical-align: top;
}
